<template>
  <div class="container">
    <div class="registerBox">
      <div class="unLogin">
        <div class="doll"><img src="/img/doll.png" /></div>
        <h2>編輯個人資料</h2>
        <div class="formRow">
          <h3>Email</h3>
          <input
            id="email"
            type="email"
            name=""
            value=""
            placeholder="請輸入您的email同時為帳號"
            required
            :disabled="User.OpenID"
            v-model="User.Email"
          />
        </div>
        <div class="formRow">
          <h3>姓名</h3>
          <input
            id="name"
            type="text"
            name=""
            value=""
            :disabled="User.OpenID"
            placeholder="請輸入您的姓名"
            required
            v-model="User.Name"
          />
        </div>
        <div class="formRow" v-if="!User.OpenID">
          <h3>密碼</h3>
          <input
            id="password1"
            type="password"
            name=""
            value=""
            placeholder="如不修改請留空白"
            v-model="User.Password"
          />
        </div>
        <div class="formRow" v-if="!User.OpenID">
          <h3>確認密碼</h3>
          <input
            id="password2"
            type="password"
            name=""
            value=""
            placeholder="再次輸入密碼，如不修改請留空白"
            v-model="User.Password2"
          />
        </div>
        <div class="formRow" v-else>
          <h3>密碼</h3>
          <input id="password2" type="password" disabled name="" value="" />
          <span>請到教育雲進行修改</span>
        </div>
        <div class="formRow">
          <h3>手機</h3>
          <input
            id="mobile"
            type="tel"
            name=""
            value=""
            placeholder="09xxxxxxxx"
            v-model="User.Mobile"
          /><span>海外使用者請輸入+國碼手機號碼(如+8869xxxxxxxx)</span>
        </div>
        <div class="submitBtn" v-if="!User.OpenID">
          <a @click="register" href="javascript:void(0);">修改</a>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      User: {
        Email: "",
        Name: "",
        Password: "",
        Password2: "",
        Mobile: "",
        OpenID: "",
      },
    };
  },
  async mounted() {
    this.setBase("register");
    var response = await this.$api.getMe();
    this.User.Email = response.Email;
    this.User.Name = response.Name;
    this.User.Mobile = response.Mobile;
    this.User.OpenID = response.OpenID;
  },
  methods: {
    async register() {
      if (!this.validate(".registerBox")) {
        return;
      }
      if (this.User.Password != this.User.Password2) {
        alert("兩次密碼不一致，請重新輸入！");
        return;
      }
      if (
        !/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[a-zA-Z\d]{8,}$/.test(
          this.User.Password2
        )
      ) {
        alert("密碼設定需包含8位以上的英文大小寫+數字");
        return;
      }
      try {
        var result = await this.$api.updateMe(this.User);
        var user = this.$user.user;
        user.Name = this.User.Name;
        this.$user.setUser(user);
        this.$router.replace("/");
      } catch (e) {
        console.error(e);
        if (e.Status == 4) {
          alert("此帳號已註冊過");
        }
      }
    },
  },
};
</script>